import {Directive, HostListener, ElementRef, HostBinding, OnInit} from '@angular/core';
declare var $:any;

@Directive({
  selector: '[saMenuCollapse]'
})
export class MenuCollapseDirective implements OnInit {
  @HostBinding('class.open') open:boolean;

  private el:HTMLElement;

  constructor(el:ElementRef) {
    this.el = el.nativeElement;
  }


  ngOnInit():any {
    let sign = $('<b class="collapse-sign"><em class="fa fa-plus-square-o"/></b>')
    // $(this.el)

    $('>a', this.el).on('click', (e)=>{
      this.open = !this.open;
      $('>ul', this.el).slideToggle(this.open);
      $('>a>.collapse-sign>em', this.el)
        .toggleClass('fa-plus-square-o', !this.open)
        .toggleClass('fa-minus-square-o', this.open);
      e.stopPropagation();
      return false;
    }).append(sign);

  }


}
